<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 提示信息 -->
            <a-form-model-item
              label="设备家族名称"
              prop="resourceFamilyName"
              ref="resourceFamilyName"
              
            >
              <a-input
                v-model="entity.resourceFamilyName"
                placeholder="dddddd"
                name="resourceFamilyName"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model:value="entity.description"
                placeholder="描述"
                :rows="4"
                :max-length="1000"
                name="description"
              />
             
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="测试">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>admin</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 最近修改时间 -->
              <a-form-model-item label="修改时间">
                <span> 2025-04-20 15:00</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";
export default {
  name: "CreateOrEditComp",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        resourceFamilyName: "",
      },
      rules: {
        resourceFamilyName: [
          { required: true, message: "不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            trigger: "change",
            message: "ddddd",
          },
        ],
      },
    };
  },

  mounted() {},

  methods: {
    // 初始化
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求
        setTimeout(() => {
          this.loading = false;

          this.entity = {
            resourceFamilyName: "测试",
            description: "描述",
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    resetFormData() {
      // 重置表单绑定的数据源
      this.entity = {
        resourceFamilyName: "",
      };
    },
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.resourceFamilyName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        });
      }
    },

   
    inChange(e){
      console.log(e.target.value);
    }
  },
};
</script>

<style lang="scss" scoped></style>
